import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api.service';
import { HttpClient, HttpEventType } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { NgxSpinnerService } from "ngx-spinner";
import { Router } from "@angular/router";

@Component({
  selector: 'app-radio-announceradd',
  templateUrl: './radio-announceradd.component.html',
  styleUrls: ['./radio-announceradd.component.css']
})
export class RadioAnnounceraddComponent implements OnInit {

  banner: File[] = [];
	files: File[] = [];
	itemBanner: File = null;
	itemFiles: File = null;
	announcer_name: any = '';
	announcer_sosmed: any = '';
	
	isSuccessAlert: any = false;
	announcer: any = [];
	isDangerAlert: any = false;

  constructor(
  	public api: ApiService,
  	public http: HttpClient,
  	public spinner: NgxSpinnerService,
  	public router: Router
  	) { }
	

  ngOnInit(): void {
  	
  }


  onSelect(event, type) {
		if(type=='files'){
			// console.log(event);
	  		this.files.push(...event.addedFiles);
	  		console.log(this.files);
	  		this.itemFiles = this.files[0];
		}
		else if(type=='banner'){
			console.log(event);
	  		this.banner.push(...event.addedFiles);
	  		this.itemBanner = this.banner[0];
	  		 // const formData = new FormData();
    
	     //    for (var i = 0; i < this.banner.length; i++) { 
	     //      formData.append("banner[]", this.banner[i]);
	     //    }
	     //    formData.append("param", "banner");
	   
	     //    this.http.post(this.api.uploadUrl+'upload.php', formData)
	     //    .subscribe(res => {
	     //       console.log(res);
	     //       alert('Uploaded Successfully.');
	     //    })
		}
	  
	}
	 
	onRemove(event, type) {
		if(type=='files'){
			console.log(event);
	  		this.files.splice(this.files.indexOf(event), 1);
		}
		else if(type=='banner'){
			console.log(event);
	  		this.banner.splice(this.banner.indexOf(event), 1);
		}
	  
	}

	saveProgram(){
		this.spinner.show();
		
	  	const fd = new FormData();
	  	fd.append('param', 'create');
	  	fd.append('announcer_name', this.announcer_name);
	  	fd.append('announcer_sosmed', this.announcer_sosmed);
	  	fd.append('banner[]', this.itemBanner);

	    this.http.post(this.api.uploadUrl+'upload-announcer.php', fd)
		        .subscribe(res => {
		        this.spinner.hide();
		           console.log(res);
		           	this.isSuccessAlert = true;

					setTimeout(()=>{ 
					    this.moveTo('radio-announcer/radio-announcerlist');

					 }, 2000);
		           // alert('Uploaded Successfully.');
		        });
	}

	moveTo(url){
		this.router.navigate(['/'+url]);
	}

}
