import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RadioAnnouncerComponent } from './radio-announcer.component';
import { RadioAnnouncerlistComponent } from './radio-announcerlist.component';
import { RadioAnnounceraddComponent } from './radio-announceradd.component';
import { RadioAnnouncereditComponent } from './radio-announceredit.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Penyiar Radio'
    },
    children: [
      {
        path: '',
        redirectTo: 'radio-announcerlist'
      },
      {
        path: 'radio-announcerlist',
        component: RadioAnnouncerlistComponent,
        data: {
          title: 'Daftar Penyiar Radio'
        }
      },
      {
        path: 'radio-announceradd',
        component: RadioAnnounceraddComponent,
        data: {
          title: 'Tambah Penyiar Radio'
        }
      },
      {
        path: 'radio-announceredit',
        component: RadioAnnouncereditComponent,
        data: {
          title: 'Ubah Penyiar Radio'
        }
      }
    ]
  }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RadioAnnouncerRoutingModule { }
